import Vue from 'vue';
import App from './App.vue';
import store from './store';
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false;
Vue.config.devtools =
	location.href.toLowerCase().indexOf('localhost') > -1 ||
	location.href.toLowerCase().indexOf('bastest') > -1
		? true
		: false;

new Vue({
	store,
	vuetify,
	render: (h) => h(App),
}).$mount('#app');
