<template>
  <v-card color="transparent" elevation="0">
    <v-card-title class="pa-0 pb-3">
      <v-row dense justify="space-between" align="center">
        <v-col>
          <span>Session History</span>
        </v-col>
        <v-col cols="4">
          <v-select
            :disabled="sessionLoading"
            dense
            v-model="rangeSelect"
            :items="range"
            label="Days"
            outlined
            class="white"
            hide-details
          ></v-select>
        </v-col>
      </v-row>
    </v-card-title>
    <v-fade-transition mode="out-in">
      <template v-if="sessionLoading">
        <div class="text-center">
          <v-progress-circular
            color="deep-purple accent-4"
            size="72"
            indeterminate
          ></v-progress-circular>
          <h5 class="text-h5">Loading</h5>
        </div>
      </template>
      <template v-else-if="noHistory && !sessionLoading">
        <div key="noData" class="text-center">
          <v-icon size="72" color="deep-purple accent-4"> mdi-magnify </v-icon>
          <h5 class="text-h5">
            No Session History available for <b>{{ rangeSelect }}</b> days.
          </h5>
        </div>
      </template>
      <template v-else>
        <v-expansion-panels key="loadedData" multiple>
          <v-expansion-panel
            v-for="item in sessionHistory.session_data"
            :key="item.stn"
          >
            <v-expansion-panel-header>
              {{ item.start_time }}
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-timeline dense>
                <v-timeline-item
                  fill-dot
                  small
                  v-for="detail in item.details"
                  :key="detail.req_time"
                >
                  <v-row>
                    <v-col cols="12" md="2" class="text-no-wrap">
                      <b>
                        <template v-if="detail.title == ''"> N/A </template>
                        <template v-else>
                          {{ detail.title }}
                        </template>
                      </b>
                    </v-col>
                    <v-col cols="12" md="8" class="text-truncate">
                      {{ detail.url }}
                    </v-col>
                    <v-col cols="12" md="2">
                      {{ detail.req_time.substr(10) }}
                    </v-col>
                  </v-row>
                </v-timeline-item>
              </v-timeline>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </template>
    </v-fade-transition>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import _ from "lodash";
export default {
  name: "sessionHistory",
  computed: {
    ...mapGetters(["sessionHistory", "sessionLoading"]),
    noHistory() {
      var self = this;
      return _.isEmpty(self.sessionHistory);
    },
  },
  methods: {
    ...mapActions(["getSessionHistory", "setSessionLoading"]),
  },
  watch: {
    rangeSelect() {
      var self = this;
      self.setSessionLoading({ loading: true });
      setTimeout(function () {
        self.getSessionHistory();
        self.setSessionLoading({ loading: false });
      }, 4000);
    },
  },
  created() {
    var self = this;
    self.rangeSelect = Math.min(...self.range);
    self.setSessionLoading({ loading: true });
    setTimeout(function () {
      self.getSessionHistory();
      self.setSessionLoading({ loading: false });
    }, 4000);
  },
  data() {
    return {
      rangeSelect: null,
      range: [5, 15, 30, 60, 90],
    };
  },
};
</script>

