<template>
  <v-card>
    <v-card-text>
      <v-timeline dense>
        <v-timeline-item color="orange" fill-dot large icon="mdi-arrow-down">
        </v-timeline-item>
        <v-timeline-item
          color="pink"
          fill-dot
          medium
          :icon="eventType(item.type)"
          v-for="item in accessHistory"
          :key="item.record_id"
        >
          <v-row justify="space-between">
            <v-col class="text-left" cols="5" v-text="item.type"> </v-col>
            <v-col cols="7" class="text-right">
              <strong>{{ item.date }} <br />{{ item.time }}</strong>
            </v-col>
          </v-row>
        </v-timeline-item>
      </v-timeline>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "credentialHistory",
  computed: {
    ...mapGetters(["accessHistory"])
  },
  methods: {
    eventType(x) {
      switch (x) {
        case "Reset Password":
          return "mdi-form-textbox-password";
        case "Change Password":
          return "mdi-form-textbox-password";
        case "Request Password":
          return "mdi-lifebuoy";
        case "Request Username":
          return "mdi-account";
        case "Change Username":
          return "mdi-account-key";
        default:
          return "mdi-help";
      }
    }
  }
};
</script>

