<template>
  <v-card color="transparent" elevation="0">
    <v-card-title class="pa-0 pb-3">
      <v-row dense>
        <v-col> <h6 class="text-h6">Access Trend</h6></v-col>
        <v-col cols="4">
          <v-select
            :disabled="overlay"
            dense
            v-model="yearSelect"
            :items="years"
            label="Year"
            outlined
            class="white"
            hide-details
          ></v-select
        ></v-col>
      </v-row>
    </v-card-title>
    <v-card class="overflow-hidden">
      <v-overlay :value="overlay" absolute>
        <v-progress-circular indeterminate size="64"></v-progress-circular>
        <h5 class="text-h5">Loading</h5>
      </v-overlay>
      <fusioncharts
        :type="type"
        :width="width"
        :height="height"
        :dataFormat="dataFormat"
        :dataSource="dataSource"
      ></fusioncharts>
    </v-card>
  </v-card>
</template>

<script>
import Vue from "vue";
import VueFusionCharts from "vue-fusioncharts";
import FusionCharts from "fusioncharts";
import Charts from "fusioncharts/fusioncharts.charts";
import FusionTheme from "fusioncharts/themes/fusioncharts.theme.fusion";
Vue.use(VueFusionCharts, FusionCharts, FusionTheme);
Charts(FusionCharts);

export default {
  name: "accessHistory",
  data() {
    return {
      yearSelect: null,
      years: [2020, 2019, 2018, 2017, 2016, 2015],
      sampleData: [
        {
          Month: "01",
          Count: 50,
        },
        {
          Month: "02",
          Count: 66,
        },
        {
          Month: "03",
          Count: 127,
        },
        {
          Month: "04",
          Count: 70,
        },
        {
          Month: "05",
          Count: 93,
        },
        {
          Month: "06",
          Count: 72,
        },
        {
          Month: "07",
          Count: 58,
        },
        {
          Month: "08",
          Count: 48,
        },
        {
          Month: "09",
          Count: 47,
        },
        {
          Month: 10,
          Count: 38,
        },
      ],
      overlay: false,
      type: "",
      width: "100%",
      height: "350",
      dataFormat: "json",
      dataSource: {
        chart: {
          showhovereffect: "1",
          showLabels: "1",
          drawcrossline: "1",
          yaxisname: "Sessions per Month",
          rotatelabels: "1",
          setadaptiveymin: "1",
          plottooltext:
            '<div class="subtitle-1">$label</div><div class="text-h5">$dataValue</div>',
          theme: "fusion",
        },
        data: [],
      },
    };
  },
  watch: {
    yearSelect() {
      var self = this;
      self.setAccess();
    },
  },
  methods: {
    monthName(r) {
      switch (r) {
        case "01":
          return "Jan";
        case "02":
          return "Feb";
        case "03":
          return "Mar";
        case "04":
          return "Apr";
        case "05":
          return "May";
        case "06":
          return "June";
        case "07":
          return "July";
        case "08":
          return "Aug";
        case "09":
          return "Sep";
        case "10":
          return "Oct";
        case "11":
          return "Nov";
        case "12":
          return "Dec";
      }
    },
    setAccess() {
      var self = this;
      self.overlay = true;
      self.dataSource.data = [];
      self.type = "";
      setTimeout(function () {
        self.dataSource.data = self.sampleData.map(function (n) {
          return {
            value: n.Count,
            label: self.monthName(n.Month),
          };
        });

        setTimeout(function () {
          self.overlay = false;
          self.type = "spline";
        }, 500);
      }, 3000);
    },
  },
  created() {
    var self = this;
    self.yearSelect = Math.max(...self.years);
    self.overlay = true;
    self.setAccess();
  },
};
</script>
