<template>
  <v-card>
    <v-sheet
      style="position: relative"
      height="125"
      class="white--text d-flex align-end"
      color="deep-purple accent-4"
    >
      <v-card-title>
        <v-avatar size="56">
          <v-img
            max-width="56"
            max-height="56"
            lazy-src=""
            alt="user"
            src="https://cdn.vuetifyjs.com/images/profiles/marcus.jpg"
          ></v-img>
        </v-avatar>
        <div class="ml-3">John Doe</div>
      </v-card-title>
    </v-sheet>
    <v-card-text class="pa-0">
      <v-list two-line>
        <v-list-item>
          <v-list-item-icon>
            <v-icon> mdi-account </v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title> Active </v-list-item-title>
            <v-list-item-subtitle> Status </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action>
            <v-tooltip bottom open-delay="250">
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon color="primary" v-bind="attrs" v-on="on">
                  <v-icon> mdi-shield-account </v-icon>
                </v-btn>
              </template>
              <span>Lock User Account</span>
            </v-tooltip>
          </v-list-item-action>
        </v-list-item>
        <v-list-item>
          <v-list-item-icon></v-list-item-icon>
          <v-list-item-content>
            <v-fade-transition mode="out-in">
              <template v-if="userInfo[0].username != ''">
                <v-list-item-title>
                  {{ userInfo[0].username }}
                </v-list-item-title>
              </template>
            </v-fade-transition>
            <v-list-item-subtitle> Username </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action>
            <v-tooltip bottom open-delay="250">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  color="primary"
                  v-bind="attrs"
                  v-on="on"
                  @click="changeUsernamePop = true"
                >
                  <v-icon> mdi-account-key </v-icon>
                </v-btn>
              </template>
              <span>Change Username</span>
            </v-tooltip>
          </v-list-item-action>
        </v-list-item>
        <v-divider inset></v-divider>
        <v-list-item>
          <v-list-item-icon>
            <v-icon> mdi-form-textbox-password </v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>
              Permanent
              <v-icon color="green" class="ml-2">mdi-check</v-icon>
            </v-list-item-title>
            <v-list-item-subtitle> Password </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action>
            <v-tooltip bottom open-delay="250">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  color="primary"
                  v-bind="attrs"
                  v-on="on"
                  @click="pwConfirm = true"
                >
                  <v-icon>mdi-lock-reset</v-icon>
                </v-btn>
              </template>
              <span>Reset password</span>
            </v-tooltip>
          </v-list-item-action>
        </v-list-item>
        <v-list-item>
          <v-list-item-icon></v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title> 10/1/2020 </v-list-item-title>
            <v-list-item-subtitle> Last Update </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-divider inset></v-divider>
        <v-list-item
          three-line
          v-for="(item, index) in emailList"
          :key="item.email + item.type"
        >
          <v-list-item-icon>
            <template v-if="index == 0">
              <v-icon> mdi-email </v-icon>
            </template>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>
              <span class="text-truncate">
                <a :href="'mailto:' + item.email">{{ item.email }}</a>
              </span>
            </v-list-item-title>
            <v-list-item-subtitle class="d-flex justify-space-between">
              <span>{{ item.type }}</span>
            </v-list-item-subtitle>
            <v-list-item-subtitle>
              <template v-if="item.verified == 1">
                <v-chip x-small color="green" label text-color="white">
                  Verified
                </v-chip>
              </template>
              <template v-else>
                <v-chip
                  x-small
                  color="orange darken-1"
                  label
                  text-color="white"
                >
                  Pending
                </v-chip>
              </template>
            </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action>
            <v-btn icon @click="makePreferred(item.email)">
              <v-icon :color="preferredEmail(item.preferred)">
                mdi-star
              </v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
      </v-list>
    </v-card-text>
    <v-dialog v-model="pwConfirm" max-width="400" persistent>
      <v-card>
        <v-card-title> Password Reset </v-card-title>
        <v-card-text>
          This will reset the Password for <b>{{ userInfo[0].username }}</b
          >.
          <template v-if="verEmailList.length > 1">
            <div>
              <p>Where would you like to send the new Temporary Password?</p>
              <v-radio-group v-model="chosenEmail">
                <v-radio
                  v-for="item in verEmailList"
                  :key="item.email + item.type"
                  :label="item.email"
                  :value="item.email"
                >
                </v-radio>
              </v-radio-group>
            </div>
          </template>
          <template v-else>
            A new Temporary password will be sent to
            <b>{{ preferredEmailSel }}</b>
          </template>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="pwConfirm = false" :disabled="pwResetting"
            >Cancel</v-btn
          >
          <v-btn
            text
            @click="pwReset"
            :disabled="emailSelected"
            color="deep-purple accent-4"
            :loading="pwResetting"
            >Submit</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="changeUsernamePop" max-width="350">
      <v-card>
        <v-card-title>Change Username</v-card-title>

        <v-card-text>
          <v-row dense>
            <v-col cols="12">
              <v-text-field
                :disabled="updatingUsername"
                outlined
                label="New Username"
                type="text"
                v-model="newUname"
              >
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field
                :disabled="updatingUsername"
                outlined
                label="Confirm New Username"
                type="text"
                v-model="confirmNewUname"
              >
              </v-text-field>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            :disabled="updatingUsername"
            text
            @click="changeUsernamePop = false"
            >Cancel</v-btn
          >
          <v-btn
            :loading="updatingUsername"
            color="primary"
            text
            @click="updateUsername"
            :disabled="!matchUname"
            >Submit</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
export default {
  name: "userInfoCard",
  data() {
    return {
      updatingUsername: false,
      newUname: "",
      confirmNewUname: "",
      changeUsernamePop: false,
      chosenEmail: "",
      pwResetting: false,
      pwConfirm: false,
      userInfo: [
        {
          username: "DoeJoh",
          status: "Active",
        },
      ],
      emailList: [
        {
          email: "test@test.com",
          type: "Personal",
          preferred: "1",
          verified: "1",
        },
        {
          email: "test@testwork.com",
          type: "Work",
          preferred: "0",
          verified: "0",
        },
      ],
    };
  },
  computed: {
    matchUname() {
      var self = this;
      if (self.newUname != "") {
        if (self.newUname == self.confirmNewUname) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    emailSelected() {
      var self = this;
      if (self.verEmailList > 1) {
        if (self.chosenEmail == "") {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    verEmailList() {
      var self = this;
      return self.emailList.filter(function (n) {
        return n.verified == 1;
      });
    },
    preferredEmailSel() {
      var self = this;
      return self.emailList.filter(function (n) {
        return n.preferred == 1;
      })[0].email;
    },
  },
  methods: {
    updateUsername() {
      var self = this;
      self.updatingUsername = true;
      setTimeout(function () {
        self.userInfo[0].username = self.confirmNewUname;
        self.changeUsernamePop = false;
        (self.newUname = ""), (self.confirmNewUname = "");
        self.updatingUsername = false;
      }, 2000);
    },
    pwReset() {
      var self = this;
      self.pwResetting = true;
      setTimeout(function () {
        self.pwConfirm = false;
        self.pwResetting = false;
      }, 2000);
    },
    makePreferred(r) {
      var self = this;
      self.emailList.filter(function (n) {
        return n.email == r;
      })[0].preferred = 1;
      self.emailList.filter(function (n) {
        return n.email != r;
      })[0].preferred = 0;
    },
    preferredEmail(r) {
      if (r == 1) {
        return "yellow darken-1";
      } else {
        return "";
      }
    },
  },
};
</script>

<style>
</style>